<template>   
    <v-container>     
        <v-card>       
            <v-container grid-list-md mb-12>
            <h2 class="text-md-center">Transaction List</h2>         
            <v-layout row wrap style="margin:10px">           
                <v-flex xs6 class="text-right">               
                    <v-text-field                 
                    v-model="keyword"                 
                    append-icon="mdi-search"                 
                    label="Search"                 
                    hide-details               
                    ></v-text-field>           
                </v-flex> 
                <v-flex>          
                </v-flex> 
                <v-flex offset-xs4>           
                    <v-btn               
                    depressed               
                    dark               
                    rounded
                    href="http://localhost:8080/RESTSERVER_PAW/index.php/Report/transaction" 
                    :hidden="hide"        
                    style="text-transform: none !important;"               
                    color = "green accent-3"              
                    >               
                    <v-icon size="18" class="mr-2">mdi-pencil-plus</v-icon>                   
                        Report               
                    </v-btn>           
                </v-flex>                      
            </v-layout> 
            Transaction
            <v-data-table             
            :headers="headers"             
            :items="users"             
            :search="keyword"             
            :loading="load"           
            >             
            <template v-slot:body="{ items }">               
                <tbody>                 
                    <tr v-for="(item) in items" :key="item.id" @click="setItem(item.id_transaction)">                   
                        <td>{{ item.id_transaction }}</td>                   
                        <td>{{ item.name }}</td>                   
                        <td>{{ item.type}}</td>                   
                        <td>{{ item.total }}</td>                  
                        <td >                     
                            <v-btn                        
                            icon                       
                            color="indigo"                       
                            light                       
                            @click="editHandler(item)"                     
                            >                       
                            <v-icon>mdi-pencil</v-icon>                     
                            </v-btn>                     
                            <v-btn 
                            icon                       
                            color="error"                       
                            light                       
                            @click="deleteData(item.id_transaction)"                     
                            >                       
                                <v-icon>mdi-delete</v-icon>                     
                            </v-btn>                   
                        </td>                 
                    </tr>               
                </tbody>             
            </template>           
        </v-data-table>
            Item       
            <v-data-table             
            :headers="headers2"             
            :items="itemsTransaction"             
            :search="keyword"             
            :loading="load"           
            >             
            <template v-slot:body="{ items }">               
                <tbody>                 
                    <tr v-for="(item) in items" :key="item.id">                   
                        <td>{{ item.id_transaction }}</td>                   
                        <td>{{ item.item }}</td>                   
                        <td>{{ item.quantity }}</td>                   
                        <td>{{ item.type}}</td>                   
                        <td>{{ item.price }}</td>                 
                                         
                    </tr>               
                </tbody>             
            </template>           
        </v-data-table>       
    </v-container>     
    </v-card>     
    <v-dialog v-model="dialog" persistent max-width="600px">       
        <v-card>         
            <v-card-title>           
                <span class="headline">Admin</span>         
                </v-card-title>         
                <v-card-text>           
                    <v-container>             
                        <v-row>               
                            <v-col cols="12">                 
                                <v-text-field label="Name*" v-model="form.name" required></v-text-field>               
                                </v-col>              
                                                 
                                    <v-col cols="12">                 
                                        <v-select
                                            v-model="form.type"
                                            :items="items"
                                            label="Type*"
                                            dense
                                        ></v-select>               
                                    </v-col>           
                                    </v-row>           
                                </v-container>           
                                <small>*indicates required field</small>         
                            </v-card-text>         
                            <v-card-actions>           
                                <v-spacer></v-spacer>           
                                <v-btn color="blue darken-1" text @click="resetForm()">Close</v-btn>           
                                <v-btn color="blue darken-1" text @click="setForm()">Save</v-btn>         
                                </v-card-actions>       
                            </v-card>     
                        </v-dialog>     
                    <v-snackbar       
                    v-model="snackbar" 
                    :color="color"       
                    :multi-line="true"       
                    :timeout="3000"     
                    >       
                    {{ text }}       
                    <v-btn         
                     dark         
                     text         
                     @click="snackbar = false"       
                    >         
                        Close       
                    </v-btn>     
                </v-snackbar>   
            </v-container> 
        </template> 
<script> 
import { log } from 'util'
export default {    
    data () {       
        return {  
            hide: true,
            menu2: false,      
            dialog: false,      
            keyword: '',
            id_user: '', 
            items: ['Dine In', 'Take Away'],              
            headers: [             
                {               
                    text: 'ID Transaction',               
                    value: 'id_transaction',             
                },             
                {               
                    text: 'Name',               
                    value: 'name'             
                },             
                {               
                    text: 'Type',               
                    value: 'type'             
                },             
                {               
                    text: 'Total',               
                    value: 'total'             
                },             
                {               
                    text: 'Aksi',               
                    value: null             
                },         
            ],
            headers2: [             
                {               
                    text: 'ID',               
                    value: 'id',             
                },             
                {               
                    text: 'Item',               
                    value: 'item',             
                },             
                {               
                    text: 'Quantity',               
                    value: 'quantity'             
                },             
                {               
                    text: 'Type',               
                    value: 'type'             
                },             
                {               
                    text: 'Price',               
                    value: 'price'             
                }       
            ],
            itemsTransaction:[],         
            users: [],         
            snackbar: false,          
            color: null,         
            text: '',          
            load: false, 
            form: {            
                name : '',  
                type: ''        
            },         
            user : new FormData,         
            typeInput: 'new',        
            errors : '',         
            updatedId : '',       
        }     
    },     
    methods:{
        setItem(id){
            var uri = this.$apiUrl + '/TransactionItem/' + id             
            this.$http.get(uri).then(response =>{                 
                this.itemsTransaction=response.data.message             
            }) 
        },         
        getData(){
            // var config = {
            //     headers: {
            //         Authorization: 'Bearer ' + localStorage.getItem('token')
            //     }
            // }
            if(localStorage.getItem('role') == "Admin"){
                var uri = this.$apiUrl + '/Transaction'     
            }else
                var uri = this.$apiUrl + '/Transaction/where/' + localStorage.getItem('id')
            this.$http.get(uri).then(response =>{                 
                this.users=response.data.message             
            })               
        },                  
        updateData(){             
            this.user.append('name', this.form.name);             
            this.user.append('type', this.form.type);      
            var uri = this.$apiUrl + '/Transaction/' + this.id_user + '/'+ this.updatedId;             
            this.load = true             
            this.$http.post(uri,this.user).then(response =>{ 
                this.snackbar = true; //mengaktifkan snackbar               
                this.color = 'green'; //memberi warna snackbar               
                this.text = response.data.message; //memasukkan pesan ke snackbar               
                this.load = false;               
                this.dialog = false               
                this.getData(); //mengambil data user               
                this.resetForm();               
                this.typeInput = 'new';
                this.pass = false;         
            }).catch(error =>{               
                this.errors = error               
                this.snackbar = true;               
                this.text = 'Try Again';               
                this.color = 'red';               
                this.load = false;               
                this.typeInput = 'new';
                this.pass = false;           
            })         
        },         
        editHandler(item){           
            this.typeInput = 'edit';
            this.pass = true;           
            this.dialog = true;           
            this.form.name = item.name;           
            this.form.type = item.type;           
            this.id_user = item.id_user;          
            this.updatedId = item.id_transaction;         
        },
        
        deleteData(deleteId){ //mengahapus data
            // var config = {
            //     headers: {
            //         Authorization: 'Bearer ' + localStorage.getItem('token')
            //     }
            // }              
            var uri = this.$apiUrl + '/Transaction/' + deleteId; //data dihapus berdasarkan id 
            this.$http.delete(uri).then(response =>{                 
                this.snackbar = true;                 
                this.text = response.data.message;                 
                this.color = 'green'                 
                this.deleteDialog = false;                 
                this.getData();             
            }).catch(error =>{                 
                this.errors = error                 
                this.snackbar = true;                 
                this.text = 'Try Again';                 
                this.color = 'red'; 
            })         
        },
        
        setForm(){             
            if (this.typeInput === 'new') {               
                this.sendData()             
            } else {               
                this.updateData()             
            }         
        },         
        resetForm(){             
            this.form = {               
                name : '',               
                email : '',               
                password : '',
                gender: '',         
                address: '',         
                email : '',           
                password : ''             
            }
            this.dialog = false      
        }     
    },     
    mounted(){         
        this.getData(); 
         if(localStorage.getItem('role') == "Admin"){
                this.hide = false       
            }else
                this.hide = true    
        }, 
    } 
</script> 
  